:root {
  --background: #93b6f9de;
  --text: #e8e8f0;
  --muted: #a3a3b2;
  --brand: #6aa9ff;
  --brand-600: #0d9e69;
  --surface: #6161a3;
  --card: #ffffff;
  --border: #6868c6;
  --radius: 16px;
  --container: 1100px;
  --front-sans: ui-sans-serif, system-ui, -apple-system, 
    BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, 
    "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", 
    "Segoe UI Symbol", "Noto Color Emoji";
  --shadow: 010px 30px rgba(0, 0, 0, 0.25);
  --foreground: #090909;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.6;
}
.site-header {
  position: sticky;
  top: 0;
  z-index: 50;
  backdrop-filter: blur(10px);
  background: linear-gradient(300deg, rgba(243, 96, 63, 0.8), rgba(51, 57, 224, 0.666) 100%, rgba(89, 52, 236, 0));
  border-bottom: 1px solid var(--border);
}
.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .9rem 0;
}
.brand {
  font-weight: 700;
  font-size: 1.1rem;
}
.menu {
  list-style: none;
  display: flex;
  gap: 1rem;
  margin: 0;
  padding: 0;
}
.menu a {
  color: #000000;
  opacity: 0.9;
}
.menu a:hover {
  opacity: 1;
}
a:hover {
  cursor: pointer;
}
.container {
  width: min(100% - 2rem, var(--container));
  margin-inline: auto;
}
.section {
  padding: 3rem 0;
}
.section.center {
  text-align: center;
}
h1, h2, h3 {
  line-height: 1.2;
  margin: 0 0 .5rem;
}
h1 {
  font-size: 3rem;
}
p {
  margin: 0 0 1rem;
}
.btn {
  display: inline-block;
  padding: .75rem 1rem;
  border-radius: 999px;
  background: var(--brand-600);
  color: white;
  font-weight: 600;
  border: 1px solid transparent;
}
.grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(12, 1fr);
}
.card {
  background: var(--card);
  color: #090909;
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 1.2rem;
  box-shadow: var(--shadow);
}
h3 {
  font-size: 1.8rem;
}
.col-12 {
  grid-column: span 12;
}
.col-6 {
  grid-column: span 6;
}
.col-4 {
  grid-column: span 4;
}
.col-3 {
  grid-column: span 3;
}
@media (max-width: 900px) {
  .col-md-12 {
    grid-column: span 12 !important;
  }
  .col-md-6 {
    grid-column: span 6 !important;
  }
}
@media (max-width: 640px) {
  .col-sm-12 {
    grid-column: span 12 !important;
  }
}